import styled from "styled-components";

export const CalendarContainer = styled.main`
  margin: 0 auto;
  margin-top: 2rem;
  width: 80vw;
  height: calc(100vh - 100px);
  overflow: auto;

  .scheduler-table {
    .dx-scheduler-header-panel-cell {
      background-color: var(--background-color);
      font-weight: bold;
    }
  }
`;
